}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button:focus {
  outline: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.btn {
  min-width: 161px;
  height: 44px;
  cursor: pointer;
  text-align: center;
  border: none;
  border-radius: 22px;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.btn-light {
  color: var(--basic-title-color);
  background-color: #fff;
}

.btn-light:hover, .btn-light:focus {
  background-color: #ecebeb;
}

.btn-red {
  background-color: var(--basic-title-color);
  color: #fff;
}

.btn-red:hover, .btn-red:focus {
  background-color: #9b0e31;
}

.mobile__window {
  z-index: 99;
  position: relative;
}

.modal-close-btn__icon {
  width: 14px;
  height: 14px;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 32px;
}

.modal-close-btn__icon:hover, .modal-close-btn__icon:focus {
  fill: #9b0e31;
}

.menu {
  height: 100vh;
  width: 254px;
  background-color: var(--background-mobile-open);
  z-index: 100;
  padding: 58px 20px;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translateX(100vw);
  box-shadow: -8px 0 21px rgba(0, 0, 0, .25);
}

.menu.active {
  transform: translateX(0);
}

.menu__close {
  text-align: right;
}

.menu__link {
  color: #fff;
  border-top: 1px solid #e18298;
  border-bottom: 1px solid #e18298;
  align-items: center;
  padding-top: 13px;
  padding-bottom: 13px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 131%;
