}

.menu__button {
  margin-bottom: 20px;
}

.buy-now__button {
  width: 160px;
  height: 44px;
  text-align: center;
  color: var(--basic-title-color);
  background: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.buy-now__button:hover, .buy-now__button:focus {
  background-color: #ecebeb;
}

.buy-now__button svg {
  width: 8px;
  height: 20px;
  fill: none;
  stroke: var(--basic-title-color);
  stroke-width: 6.4px;
  margin-left: 21.75px;
}

button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.modal {
  box-sizing: border-box;
  min-width: 250px;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  background-color: #e17992;
  margin: auto;
  padding: 40px 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

.modal__btn {
  cursor: pointer;
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__btn:hover, .modal__btn:focus {
  color: var(--basic-title-color);
}

.modal__close-icon {
  width: 14px;
  height: 14px;
  fill: currentColor;
}

.modal__list {
  margin-bottom: 15px;
}

.modal__item {
  width: 70%;
  background-color: var(--footer-line-color);
  border-radius: 4px;
  margin: 0 auto;
  padding: 10px;
}

.modal__item:not(:last-child) {
  margin-bottom: 15px;
}

.modal__title {
  text-align: center;
  text-transform: uppercase;
  color: var(--contacts-color);
  margin-bottom: 12px;
  font-family: Titan One;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
}

.modal__text {
  letter-spacing: .04em;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
}

.modal-btns-contacts {
  box-sizing: border-box;
  width: 90%;
  height: 90%;
  background-color: #e17992;
  margin: auto;
  padding: 40px 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

.modal__overlay-container {
  border-radius: 2px;
  position: relative;
  overflow: hidden;
}

.modal__overlay-text {
  width: 100%;
  height: 100%;
  background-color: var(--basic-title-color);
  color: var(--card-tittle-color);
  letter-spacing: .04em;
  padding: 15px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  transition: transform .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.modal__item:hover .modal__overlay-text, .modal__item:focus .modal__overlay-text {
  transform: translateY(0%);
}

.modal__overlay-title {
  text-align: center;
}

.modal__icecream {
  background-color: var(--background-cream);
}

.modal__iceccoffee {
  background-color: var(--background-coffee);
}

.modal__milkshakes {
  background-color: var(--background-milkshakes);
}

